

@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


body {
    background: #D0D0D0;
    margin: 0;
    font-family:'Poppins';
}
.navbar {
background: white;
}
.logo {
    text-decoration: none;
    font-weight: bold;
    color: black;
    font-size: 1.2em;
    margin: 3px;
    position: relative;
}
span {
    color: blue;
}
nav{
    display: none;
}
.mobile-menu
{
    width: 5%;
    height: 5%;
    cursor: pointer;
}

.container {
    display: flex;
    place-content: space-between;
}
a {
color: grey;
text-decoration: none;
}
.subhead {
    font-size: large;
}
.hero-image{
    width: 50%;
    margin-top: 3em;
}
.video{

    width: 10%;
}
ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
}
section{
    padding: 5em 2em;
}
.hero{
    text-align: center;
}
.hero-image{
    width: 100%;
    height: 100%;
}
.left-col{
    .subhead{
        text-transform: uppercase;
        font-weight: bold;
        color: grey;
        letter-spacing: .5em;
    }
    h1{
        font-size: 2.5em;
        line-height: 1.3em;
        margin-top: .2em;
    }
    .primary-cta{
        background: blue;
        color: white;
        padding: .6em 1.3em;
        font-size: 1.4em;
        border-radius: 5em;
        font-weight: bold;
        display: inline-block;
    }
    .watch-video-cta{
        display: block;
        margin-top: 1em;
    }
    img{
        margin-right: .5em;
    }
}
.features-section{
    background: #20272E;
    color: white;
}
.features-section{
    margin: 0;
    padding-left: 3em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax (19rem, 1fr));
    li{
        font-size: 1.1em;
        margin-bottom: 1em;
        margin-left: 2em;
        position: relative;

        &:before{
            content: '';
            left:-2em;
            position: absolute;
            width: 20px;
            height: 20px;
            background-image: url( '../assets/check.svg');
            background-size: contain;
            margin-right: .5em;


        }
    }

}
.features-section img{
    display: none;
}
.testimonials-section{
    background: blue;
    color: white;
   
        li{
            background: #006bd6;
            text-align: center;
                padding: 2em 1em;
                width: 80%;
                margin: 0 auto 5em auto;
                border-radius: 1em;
            img{
                width: 5em;
                height:5em;
                border:3px solid #006bd6;
                border-radius: 50%;
                margin-top: -4.5em;
                
            }
        }
    
}
h2{
    font-size: 2em;

}
label{
    display: block;
    font-size: 1.2em;
    margin-bottom: .5em;
}
input, textarea{
    width: 100%;
    padding: .8em;
    margin-bottom: 1em;
    border-radius: .3em;
    border: 1px solid gray;
    box-sizing: border-box;
}
.send-message-cta{
    background-color: blue;
    color: white;
    font-weight: bold;
    font-size: 1.3em;
    border: none;
    margin-bottom: 5em;
    border-radius: 5em;
    display: inline-block;
    padding: .8em 2em;
    width: unset;
    cursor:pointer;
}
.gmap_iframe{
    width: 100%;
    height: 300px;
}
nav.menu-btn{
    display: block;
}
nav{
    position: fixed;
    z-index: 999;
    width: 66%;
    right: 0;
    top: 0;
    background-color: #20272e;
    height: 100vh;
    padding: 1em;

    ul.primary-nav{
        margin-top: 5em;
    }
    li{
        a{
            color: white;
            text-decoration: none;
            display: block;
            padding: .5em;
            font-size: 1.3em;
            text-align: right;

            &:hover{
                font-weight: bold;
            }
        }
    }
}
.mobile-logout{
    float: right;
    margin: .5em;
    cursor: pointer;
    width: 10%;
    color: white;
}
@media only screen and(min-width:768px) {
    .mobile-menu, .mobile-logout {
        display: none;
    }
    .navbar.container{
        display: grid;
        grid-template-columns: 180px auto;
    }
    .navbar nav {
        display: flex;
        justify-content: space-between;
        background: none;
        position: unset;
        height: auto;
        width: 100%;
        padding: 0;

        ul{
            display: flex;
        }
        a{
            color: black;
            font-size: 1em;
            padding: .1em 1em;
        }
        ul.primary-nav{
            margin: 0;
        }
        li.current a{
            font-weight: bold;
        }
        li.go-premium-cta 
        a {
           
            color: blue;
            border:3px solid blue;
           
            font-weight: bold;
            border-radius: 5em;
            margin-top: -.2em;
            &:hover{
                background: blue;
                color: white;
            }
        }
    }
}
@media only  screen and ( min-width:1080px) {
    .container{
        width: 1080px;
        margin: 0 auto;
    }
section {
    padding: 10em 4em;
}
.hero.container {
    display: flex;
    justify-content: space-between;
    text-align: left;
    .left-col{
        margin: 3em 3em 0 5em;

        h1{
            font-size: 3em;
            width: 90%;
        }
    }
}
.hero-image{
    width: 30%;
    margin-right: 8em;
}

.primary-cta {
    margin-right: 1em;
}
.features-section{
    display: block;
    margin-left: 5em;
    li{
        font-size: 1.4em;
        &::before {
            width: 30px;
            height: 30px;
        }
    }
}
.features-section{
    position: relative;
}
.features-section img {
    display: block;
    right: 0;
    width: 350px;
    bottom: -2em;
}
.testimonials-section ul{
    display: flex;
    li {
        margin:  0 1em;
    }
}
.contact-section {
    position: relative;
    .container {
        display: flex;
    }
}
.contact-right {
    position: absolute;
    right: 0;
    top:0;
    width: 45%;
    height: 100%;

    .gmap_canvas {
        height: 100%;
    }
}
}
@media  ONLY screen and (min-width:1450px)
 {
     .features-section{
         &:before{
             content: '';
             position: absolute;
             width: 10%;
             height: 20em;
             background: blue;
             left:0;
             top:-4em;
         }
         &:after{
            content: '';
            position: absolute;
            width: 5%;
            height: 20em;
            background: url('../assets/dots.svg') no-repeat;
            left:10em;
            top:5em;
            
        }
     }
    
}
    
